<template>
  <div id="main">
    <div class="form">
      <form class="reg" v-on:submit.prevent="addedm">
        <input type="text" placeholder="name" v-model="newedm.name">
        <input type="text" placeholder="symbol" v-model="newedm.symbol">
        <input type="text" placeholder="category" v-model="newedm.category">
        <input type="submit" value="add">
      </form>
    </div>
    <div v-for="edm in list">
      <div v-if="!edm.edit">
        {{edm.name}}
        | <span class='del' v-on:click='seteditedm(edm)'>edit</span>
        | <span class='del' v-on:click='removeedm(edm)'>delete</span>
      </div>
      <div v-else>
        <form class="reg" v-on:submit.prevent="saveeditedm(edm)">
          <input type="text" placeholder="name" v-model="edm.name">
          <input type="text" placeholder="symbol" v-model="edm.symbol">
          <input type="text" placeholder="category" v-model="edm.category">
          <input type="submit" value="save">
          <input type="button" value="cancel" v-on:click='canceleditedm(edm)'>
        </form>
      </div>
    </div>
    <h1>Vue isotope</h1>
    <h2>Filter</h2>
    <div><input type="text" v-model="search" placeholder="Search title.."/></div>
    <div class="button-group">
        <button v-for="(val, key) in option.getFilterData" class="button" :class="[key===filterOption? 'is-checked' : '']" @click="filter(key)">{{key}}
      </button>
    </div>

    <h2>Sort</h2>
    <div class="button-group">
      <button class="button" :class="[sortOption==='original-order'? 'is-checked' : '']" @click="sort('original-order')">original order</button>
      <button v-for="(val, key) in option.getSortData" class="button" :class="[key===sortOption? 'is-checked' : '']" @click="sort(key)">{{key}}</button>
    </div>

    <div class="hide">
      <h2>Layout</h2>
      <button v-for="layout in layouts" :class="[layout===currentLayout? 'is-checked' : '']" class="button" @click="changeLayout(layout)">
        {{layout}}
      </button>
    </div>

    <isotope ref="cpt" id="root_isotope1" :item-selector="'element-item'" :list="filteredList" :options='option' @filter="filterOption=arguments[0]" @sort="sortOption=arguments[0]" @layout="currentLayout=arguments[0]">
      <div v-for="element,index in filteredList" :class='[element.category]'  :key="index">
        <h3 class="name">{{element.name}}</h3>
        <p class="symbol">{{element.symbol}}</p>
        <p class="number">{{element.number}}</p>
        <p class="weight">{{element.weight}}</p>
      </div>
    </isotope>
  </div>
</template>

<script>
import isotope from 'vueisotope'
import Firebase from 'firebase'
import toastr from 'toastr'

let config = {
  apiKey: "AIzaSyA5OMP0joHW-A-xgbIfWhE4cwAhy45M7VA",
  authDomain: "edmlist-001.firebaseapp.com",
  databaseURL: "https://edmlist-001.firebaseio.com",
  projectId: "edmlist-001",
  storageBucket: "",
  messagingSenderId: "469183335088"
}

let app = Firebase.initializeApp(config);
let db = app.database();
let eDMref = db.ref('edm');

var count = 0;
export default {
  name: 'eDMlist',
  firebase: {
    list: eDMref
  },
  data () {
    return {
      search: '',
      newedm:{
        name: "",
        symbol: "",
        number: 0,
        weight: 0,
        category: "",
        metal: true,
        edit: false
      },
      layouts: [
        "masonry",
        "fitRows",
        "vertical"
      ],
      list: [{}],
      currentLayout: 'masonry',
      selected: null,
      sortOption: "original-order",
      filterOption: "show all",
      option: {
        itemSelector: ".element-item",
        getFilterData: {
          "show all": function() {
            return true;
          },
          metal: function(el) {
            return !!el.metal;
          },
          transition: function(el) {
            return el.category === "transition";
          },
          "alkali and alkaline-earth": function(el) {
            return el.category === "alkali" || el.category === "alkaline-earth";
          },
          "not transition": function(el) {
            return el.category !== "transition";
          },

          "metal but not transition": function(el) {
            return !!el.metal && el.category !== "transition";
          },
          "number > 50": function(el) {
            return el.number > 50;
          },
          "name ends with ium": function(el) {
            return el.name.match(/ium$/);
          }

        },
        getSortData: {
          name: "name",
          symbol: "date-modified",
          category: "category"
        }
      }

    }
  },
  methods: {
    sort: function(key) {
      this.$refs.cpt.sort(key);
    },
    filter: function(key) {
    	this.$refs.cpt.filter(key);
      this.search='';
    },
    changeLayout: function(key) {
      this.$refs.cpt.layout(key);
    },
    addedm: function(){
      eDMref.push(this.newedm);
      this.newedm.name='';
      this.newedm.symbol='';
      this.newedm.category='';
    },
    removeedm: function(edm){
      eDMref.child(edm['.key']).remove();
      toastr.success("eDM deleted");
    },
    seteditedm: function(edm){
      eDMref.child(edm['.key']).update({edit: true});
    },
    canceleditedm: function(edm){
      eDMref.child(edm['.key']).update({edit: false});
    },
    saveeditedm: function(edm){
      eDMref.child(edm['.key']).set({name: edm.name, symbol: edm.symbol, category: edm.category, edit:false});
    }


  },
  computed: {
    filteredList() {
      return this.list.filter(element => {
        return element.name.toLowerCase().includes(this.search.toLowerCase())
      })
    }
  },
  components: {
    isotope,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>/style>



